.data-box {
    margin: 0 auto;
    margin-top: 50px;
    // margin-bottom: 20px
    // padding: 15px
    min-height: 50px;
    padding: 15px;
    width: 1140px;
    background-color: rgb(246, 246, 246);
    border-radius: 6px; }


.registration {
    &__wrapper {
        display: flex;
        flex-direction: row; }
    &__item {
        margin-right: 10px;
        height: auto;
        padding: 10px;
        margin-bottom: 10px; //* отодвигаем кнопки время вниз друг от друга */
        // border: 1px solid #8a8a8a
        border-radius: 3px;
        user-select: none;
        background: rgb(255, 255, 255);
        color: #000000;
        box-shadow: 0 1px 2px rgba(0,0,0,.16); }
    &__date {
        width: 100px;
        font-family: "PTsans";
        font-weight: bold;
        text-align: center; }
    &__reg {
        width: 1100px;
        padding-left: 7px;
        height: auto;
        font-family: "PTsans";
        font-weight: regular; }
    &__cancel-button {
        width: 100px;
        height: 24px;
        padding: 2px 2px;
        color: $color-white;
        background: #9b2900; //$color-dark-blue
        border-style: solid;
        border-width: 2px;
        border-color: #956757 #262626 #262626 #956757;
        border-radius: 5px; }
    &__cancel-button:hover {
        background: #7c2201; }
    &__cancel-button:active {
        background: #6a1c00; } }

.modal {
    --bs-modal-width: 350px; }

.modal-content {
    background-color: #4a4a4a; //var(--bs-modal-color)
    &__body-text {
        color: $color-white;
        font-size: 22px;
        text-align: center; } }

